#gallery__arrowMobile:after {
  content: "";
  transition: all 0.3s;
  border: solid #ccc;
  border-width: 0 1px 1px 0;
  position: absolute;
  margin-top: 10px;
  margin-left: 17px;
  padding: 4px;
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

.dropdown.active #gallery__arrowMobile:after {
  margin-top: 4px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.dropdown {
  position: relative;
}

.dropdown-content {
  position: absolute;
  min-width: 200px;
  overflow: auto;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
  border: 1px solid #383838;
  -webkit-animation-name: example;
          animation-name: example;
  -webkit-animation-duration: 450ms;
          animation-duration: 450ms;

  /* background: #475154; */
}

.dropdown-item:nth-child(even) {
  background-color: #232323;
}

.dropdown-item:nth-child(odd) {
  background-color: #282828;
}

@-webkit-keyframes example {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes example {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.dropdown-content a {
  color: #ffffff;
  padding: 16px 16px;
  text-decoration: none;
  display: block;
}
.dropdown a:hover {
  background-color: #383838;
}

#galleri__arrow:after {
  content: "";
  transition: all 0.3s;
  border: solid #ccc;
  border-width: 0 1px 1px 0;
  float: right;
  margin-top: 12px;
  margin-left: 17px;

  padding: 5px;
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

.dropdown.active #galleri__arrow:after {
  margin-top: 5px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

.dropdown-item:not(:last-child) {
  border-bottom: 1px solid #666;
}

