.serviceCard {
  /* border: 1px solid green; */
  flex-basis: 100%;
  margin: 1rem;
  /* height: 300px; */
  padding-bottom: 1.5rem;
  /* padding-right: 2rem;
  padding-left: 2rem; */
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
}

.serviceCard__content > h2 {
  text-align: center;
  margin: 12px 0px 12px 0px;
  font-size: 12px;
}

.serviceCard__content--p {
  text-align: center;
  margin: 12px 0px 12px 0px;
  font-size: 12px;
}

.serviceCard__content > h2 {
  font-size: 18px;
  font-weight: 500;
}

.serviceCard__btn > button {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 9px;
  display: inline-block;
  padding: 8px 25px;
  font-weight: 600;
  line-height: 2;
  letter-spacing: 2px;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #32292f;
  /* border: 1px solid white; */
  border: none;
  color: white;
  margin-top: 2rem;
}
.serviceCard__btn > button:hover {
  cursor: pointer;
}

.serviceCard__img {
  position: relative;
  /* border: 1px solid red; */
}
.serviceCard__img > img {
  max-width: 100%;
  object-fit: contain;
  display: block;
}

.overlay51 {
  z-index: 999;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 97%;
  width: 100%;
  opacity: 0;
  transition: 0.1s ease;
  background-color: #111;
}

.serviceCard__img:hover .overlay51 {
  opacity: 0.6;
  cursor: pointer;
}

